<template>
  <!-- 表单编辑完成界面 -->
  <a-layout class="container">
      <a-result 
        status="success" 
        title="您已成功发布表单" 
        sub-title="链接地址及二维码如下"
      >
        <template #extra>
          <a-layout-content class="main">
            <a-typography-paragraph :copyable="{ text: url }">点击复制链接：{{ url }}</a-typography-paragraph>
            <a-button type="primary" class="open-button" @click="openForm">打开表单</a-button>
            <qrcode-vue :value="url" :size="size" id="qrcode" />
            <a-button class="download-button" @click="() => qrcodeDownload('qrcode', 'qrcode.png')">下载</a-button>
          </a-layout-content>
        </template>
      </a-result>
    
  </a-layout>

</template>

<script setup>
import { onBeforeMount, ref } from 'vue';
import QrcodeVue from 'qrcode.vue';
import { qrcodeDownload } from '@/utils/qrcodeApi';
import { useRouter, useRoute } from 'vue-router';
import { useHttpUrl } from '@/stores/httpUrl';

const httpUrl = useHttpUrl();
const router = useRouter();
const route = useRoute();

// 地址
const url = ref("");
// 表单相应地址
const subUrl = ref("");

// 二维码大小
const size = ref(150);

// 打开表单
function openForm() {
  router.push({ name: 'publicSubmission', query: { subUrl: subUrl.value } });
}

// 在组件被挂载前调用
onBeforeMount(() => {
  subUrl.value = route?.query?.subUrl || "";
  // httpUrl.url 在 stores => httpUrl.js 中修改
  url.value =  httpUrl.url + "/user/submission/?subUrl=" + subUrl.value;
})
</script>

<style scoped>
.container {
  height: 100%;
  overflow: hidden;
}

.main {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.open-button {
  margin: 10px;
}

.download-button{
  margin: 2px;
}
</style>